*{
    box-sizing: border-box;
}
.Todo__Item{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size:18px;
    /* text-align: center; */
    font-weight: 600;
    cursor: default;
    color: #4d4d4d;
    border-bottom: 1px solid #bebebe;
    /* padding: 10px 5px; */
    display: flex;
    align-items: center;
}
.Todo__Item > p {
    margin: 0;
    flex: 1;
    max-width: 100%;
    word-wrap: break-word;
    max-width: 360px;
    padding: 14px 12px 14px 4px;
    margin-left: 4px;
}
.Todo__Item:last-child{
    border-bottom: none;
}
.Todo__Item--done > p {
    opacity: .3;
    text-decoration: line-through;
    /* padding: 10px 5px; */
}
.check-img{
    width: 28px;
    /* margin-right: 18px; */
    padding-left: 4px;
    cursor: pointer;
    opacity: 0.92;
}
.check-img:hover{
  opacity: .5;
}
.close{
    width: 18px;
    cursor: pointer;
    opacity: 0;
    margin-left: 8px;
}
.Todo__Item:hover .close{
    opacity: 1;;
}
.todo--hide{
    display: none;
}
.input__edit{
    padding: 14px 0; 
    width: 100%;
    margin-left: 10px;
    font-size: 17px;
    outline: none;
    z-index: 22;
    outline: none;
    border: 1px solid #333;
    position: relative;
}