*{
  box-sizing: border-box;
}
.App{
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    width: 100%;
    height: 100vh;
    padding-top: 30px;
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.Container{
  position: relative;
  font-size: 20px;
  width: 450px;
  margin: 0 auto;
  /* border: 1px solid #ccc; */
  box-shadow: rgba(0, 0, 0, 0.19) 3px 10px 20px;
  z-index: 55;
  background-color: #fff;
  border-radius: 3px;
  /* padding-top: 10px; */
}
.Container::after{
content: " ";
position: absolute;
top: 100%;
display: block;
left: 1px;
height: 2px;
width: 99.7%;
background-color: #ccc ;
/* border: 3px 0 0 0 solid #ccc; */
z-index: 4;
}
.Header{
  width: 100%;
  display: flex;
  align-items: center;
  /* margin-bottom: 10px; */
  border-bottom: 1px solid #ccc;
  border-top: 1px solid #ccc;
  z-index: 11;
}
.Image__CheckAll{
  width: 30px;
  padding: 5px 0px 5px 5px;
  cursor: pointer;
}
.InputBox{
 outline: none;
 border: none;
 font-size: 18px;
 padding: 20px;
 flex: 1;
 padding-left: 18px;
 opacity: 0.5;
}
.InputBox:focus{
  opacity: 1;
}
.Logo{
    font-size:60px;
    font-weight: 500;
    text-align: center;
    color: rgb(54 83 60);
    margin: 0;
}
.footer{
  color: #777;
  padding: 12px 15px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
  display: flex;
  align-items: center;
  padding-left: 9px;
}
.filters{
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  margin: 0;
}
.todo-count{
  font-size: 14px;
  font-weight: 400;
  cursor: default;
  min-width: 80px;
}
.filters__option{
  font-size: 16px;
  font-weight: 400;
  margin-left: 16px;
  padding: 2px 4px;
  cursor: pointer;
}
.clear-complete:hover{
  color: red;
}
.copyright{
  text-align:center;
  color: #333;
  text-shadow: 1px 1px #000;
  z-index: 11;
}
.clear-complete{
  font-size: 14px;
  font-weight: 400;
  margin-left: 10px;
  display: block;
  cursor: pointer;
}
.fil-active{
  border-color: rgba(175, 47, 47, 0.2);
  border: 1px solid ;
  border-radius: 3px;
}

@media (max-width: 470px) {
  .Container{
    width: 100%;
    margin: 0;
    padding: 0 12px;
  } 
  .App{
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }
}